const sankey_links = []
const num_year_range = 6
const num_clusters = 7
for (let i = 0; i < num_year_range; ++i) {
  for (let j = 0; j < num_clusters; ++j) {
    let link_instance = { source: year_range[i], target: cluster_names[j], value: year_range_count[i][j] }
    sankey_links.push(link_instance)
  }

}
